import { Component, OnInit, ViewChild, Injector, HostListener, ViewEncapsulation } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Subscription } from 'rxjs';

import { ViolationService } from 'src/app/api/controllers/Violation';
import { ViolationSetupService } from 'src/app/modules/ams/violation/violation-setup/violation-setup.service';
import { AddLookupService } from 'src/app/modules/collective/add-lookup/add-lookup.service';

import { LookupService } from 'src/app/api/controllers/Lookup';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';

import _moment from "moment-timezone";
import * as _ from 'underscore';

@Component({
  selector: 'app-violation-setup-rules',
  templateUrl: './violation-setup-rules.component.html',
  styleUrls: ['./violation-setup-rules.component.scss']
})
export class ViolationSetupRulesComponent implements OnInit {

  isDataLoaded: boolean = false;
  violationRuleHeader:any;

  totalItems:number;

  violationSearch:string = '';
  violationRuleList:any;

  

  apiSubscibe:Subscription;

  modalService:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private injector: Injector,
    private violationSetupService: ViolationSetupService,
    private addLookupService: AddLookupService,
    private violationService: ViolationService,
    private lookupService: LookupService,
    private sessionService:SessionService,
    private sharedService:SharedService
  ) {
    this.modalService = this.injector.get(ModalService)
   }

   onGlSearchFilter(event) {
    if(event != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = event;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.violationRuleHeader.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getPrintParams(event) {
    this.datagrid.exportdata(event, 'Violation Category', true, null, true, 'https://www.jqwidgets.com/export_server/dataexport.php');
  }

  addRules() {
    var id = this.sharedService.guid();
    this.violationSetupService.setViolationSetupMatDrawer({id: id, data: null, category: 'rules', isedit: false, isview: false})
  }

  @HostListener('window:onEditRules', ['$event.detail'])
  editRules(detail) {
    let data = this.datagrid.getrowdata(detail.rowId);
    this.violationSetupService.setViolationSetupMatDrawer({id: data.id, data: data, category: 'rules', isedit: true, isview: false})
  }

  @HostListener('window:onViewRules', ['$event.detail'])
  viewRules(detail) {
    let data = this.datagrid.getrowdata(detail.rowId);
    this.violationSetupService.setViolationSetupMatDrawer({id: data.id, data: data, category: 'rules', isedit: false, isview: true})
  }

  @HostListener('window:onDeleteRules', ['$event.detail'])
  deleteRules(detail) {
    this.modalService.showConfirmModal(detail.rowId);
  }

  getViolationRules() {
    
    this.isDataLoaded = false;
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    
    this.violationService.getViolationRule(params).subscribe((res: any) => {
      if (res.length > 0) {
        let violationPrivilageSourceData = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.totalItems = violationPrivilageSourceData.localdata.length;
        this.violationRuleList = new jqx.dataAdapter(violationPrivilageSourceData);
      }
      this.isDataLoaded = true;
    }, (error) => {
      this.sharedService.openSnackBar('Server Error', 'error');
    })
  }

  ngOnInit(): void {

    this.getViolationRules();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.violationRuleHeader = [ {
      text: 'category name',
      datafield: 'name',
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'inserted on',
      datafield: 'insertedOn',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ _moment(value).format("DD-MM-YYYY")+'</div>'
      },
      width: 250,
      renderer: columnrenderer
    }, {
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 100,
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2 text-secondary" onClick="viewRules(' + row + ')" ><i class="icon fa fa-eye" aria-hidden="true"></i></a> <a href="javascript:void(0)" class="mr-2" onClick="editRules(' + row + ')"><i class="icon fa fa-pencil" aria-hidden="true"></i> </a> <a href="javascript:void(0)" onClick="deleteRules(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i> </a></div>'
      },
      renderer: columnrenderer
    }];
    //delete item
    this.apiSubscibe = this.sharedService.unitlistdeleteindexcast.subscribe(id => {
      if (id != null) {
        let dataRecord = this.datagrid.getrowdata(id);
        let params = {
          lookupValueId: dataRecord.id,
          updateUserId: this.sessionService.userId
        }
        this.lookupService.deleteLookupvalue(params).subscribe((res: any) => {
          this.sharedService.setUnitListDeleteIndex(null);
          if (res.message) {
            this.datagrid.deleterow(id);
            this.datagrid.refresh();
            this.sharedService.openSnackBar(res.message, 'success');
          } else {
            this.sharedService.openSnackBar(res.errorMessage, 'error');
          }
        });
      }
    });

  }

}

let editRules = row => {
  let event = new CustomEvent('onEditRules', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editRules = editRules;

let viewRules = row => {
  let event = new CustomEvent('onViewRules', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewRules = viewRules;

let deleteRules = row => {
  let event = new CustomEvent('onDeleteRules', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).deleteRules = deleteRules;